body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "liberation_monobold";
  src: url(/static/media/liberationmono-bold-webfont.89f9e47f.woff2) format("woff2"),
    url(/static/media/liberationmono-bold-webfont.a3effeff.woff) format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "liberation_monoregular";
  src: url(/static/media/liberationmono-regular-webfont.8b7cacc1.woff2)
      format("woff2"),
    url(/static/media/liberationmono-regular-webfont.22015ce0.woff) format("woff");
  font-weight: normal;
  font-style: normal;
}

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  color: white;
  display: flex;
  flex-wrap: wrap;
}

html,
body {
  font-family: "liberation_monoregular";
}

.container {
  max-width: 80%;
  margin: 0 auto;
  text-transform: uppercase;
}

h1 {
  line-height: 1.4em;
}

a {
  color: white;
  text-decoration: underline;
}

li:hover {
  background-color: yellow;
}

li:hover a {
  color: black;
}

.smaller {
  font-size: 0.6em;
}

.rainbow {
  background: red;
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(orange),
    color-stop(yellow),
    color-stop(green),
    color-stop(cyan),
    color-stop(blue),
    to(violet)
  );
  background: linear-gradient(
    to right,
    orange,
    yellow,
    green,
    cyan,
    blue,
    violet
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.5em;
}

.recur {
  font-size: 1.3em;
  color: #fff;
  text-transform: lowercase;
  font-weight: 700;
  font-family: "Josefin Sans", sans-serif;
  background: -webkit-gradient(linear, left top, right top, from(orange), color-stop(cyan), color-stop(yellow), to(orange));
  background: linear-gradient(to right, orange, cyan, yellow, orange);
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  background-clip: text;
  text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: textclip 3.5s linear infinite;
          animation: textclip 3.5s linear infinite;
  display: inline-block;
}

@-webkit-keyframes textclip {
  to {
    background-position: 200% center;
  }
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}

.interface-list {
  list-style: none;
  line-height: 1.6em;
}

.keyWrapper {
  flex: 1 1;
  display: flex;
}

.key {
  border: 8px solid lightgray;
  border-radius: 5px;
  color: white;
  flex: 1 1;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.key:active {
  background-color: #888;
}

.keyWrapper:not(.double) {
  flex-basis: 20%;
}

.key span {
  font-size: 2.4em;
  padding: 8px 20px;
  border-bottom: 3px solid white;
}

.keyWrapper.double {
  flex: 2 1;
  flex-basis: 40%;
}

